import React from 'react'
import imagem from '../../assets/civil.png'

export default function Civil_atua() {
  return (
    <div className='flex items-center font-serif flex-col bg-color-white'>
        <div className='bg-color-white pt-12 pb-4 '>
            <h1 className='text-color-clicado font-bold text-4xl'>Direito Civil</h1>
        </div>
        <div className='hidden lg:block bg-color-white'>
            <img className='w-full' src={imagem}></img>
        </div>
        <div className='bg-color-white py-12 px-6 md:px-24'>
            <h1 className='text-color-clicado font-bold text-4xl'>O que é direito Civil?</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>O direito civil é um ramo fundamental do direito que trata das relações jurídicas entre indivíduos e entidades privadas. Abrange questões como responsabilidade civil, contratos, direito de família, sucessões, propriedade, obrigações financeiras e proteção dos direitos civis.  </h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Engloba desde litígios (Conflios de interesses) por danos pessoais até a resolução de disputas contratuais e questões de propriedade, também inclui questões de discriminação, liberdade de expressão e acesso à justiça.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>É uma área essencial do sistema jurídico que visa proteger os direitos e interesses dos indivíduos e empresas em uma variedade de situações legais. É imprencindível ter uma equipe de advogados lhe orientando em todas as etapas</h1>
        </div>
        <div className='bg-color-white py-12 px-6 md:px-24'>
            <h1 className='text-color-clicado font-bold text-4xl'>Liberdade de expressão</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Ela representa a capacidade dos indivíduos de expressar suas opiniões, ideias e pensamentos sem censura ou interferência governamental. Esta liberdade é consagrada em muitas constituições ao redor do mundo e é vista como essencial para o funcionamento saudável de uma sociedade democrática. No entanto, a liberdade de expressão não é absoluta e pode ser sujeita a restrições legais em casos de discurso de ódio, incitação à violência ou difamação.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Os tribunais frequentemente enfrentam o desafio de determinar quando a expressão ultrapassa os limites da liberdade de expressão legítima e se transforma em discurso prejudicial. O desafio reside em encontrar um equilíbrio entre proteger o direito à liberdade de expressão e garantir que essa liberdade não seja usada para incitar a violência, promover o ódio ou causar danos a terceiros.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Por este e outros motivos acreditamos que cada voz importa e merece ser protegida. Deixe-nos ajudá-lo a recuperar sua voz e a garantir que sua perspectiva seja respeitada e ouvida. Sua causa é a nossa causa, e estamos aqui para ajudar a fazer justiça.</h1>
        </div>
        <div className='bg-color-white py-12 px-6 md:px-24'>
            <h1 className='text-color-clicado font-bold text-4xl'>Proteção do Consumidor</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>A proteção do consumidor abrange uma ampla gama de questões, desde compras online e contratos de consumo até a segurança dos produtos e a transparência nas práticas comerciais. </h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>As transações comerciais são uma parte fundamental de nossas vidas. Ter seus direitos protegidos nesta área é essencial para evitar fraudes, produtos perigosos ou serviços de baixa qualidade que possam afetar sua saúde, segurança ou bem-estar financeiro.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Seja você vítima de publicidade enganosa, produtos defeituosos ou práticas comerciais desleais, estamos aqui para fornecer aconselhamento jurídico sólido e representação eficaz.</h1>
        </div>
        
        <div className='bg-color-white py-12 px-6 md:px-24'>
            <h1 className='text-color-clicado font-bold text-4xl'>Igualdade perante a Lei</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'> Esse conceito significa que todas as pessoas, independentemente de sua origem étnica, gênero, religião, orientação sexual, classe social ou qualquer outra característica pessoal, devem ser tratadas de forma equitativa perante as leis e instituições do país. Isso implica que ninguém deve receber tratamento preferencial ou discriminação injusta com base em qualquer desses fatores.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Ela assegura que todos os indivíduos tenham o mesmo acesso aos direitos, às oportunidades e à proteção legal. Além disso, promove a confiança nas instituições governamentais e fortalece a democracia, pois todos os cidadãos têm a garantia de que serão tratados com imparcialidade e justiça.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Podemos oferecer a expertise e dedicação necessárias para garantir que você receba o tratamento justo e igualitário que merece.  Estamos aqui para representar seus interesses e defender sua igualdade perante a lei com determinação e comprometimento.</h1>
        </div>
        <div className='bg-color-white py-12 px-6 md:px-24'>
            <h1 className='text-color-clicado font-bold text-4xl'>Responsabilidade Médica</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Este é um direito que se concentra nas obrigações legais dos profissionais de saúde em relação aos cuidados prestados aos pacientes. Os médicos e outros prestadores de serviços de saúde têm a responsabilidade ética e legal de fornecer cuidados médicos adequados e seguros aos pacientes.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Quando ocorrem erros médicos, negligência ou má conduta profissional que causam danos ao paciente, surge a questão da responsabilidade médica. Em casos de responsabilidade médica, é necessário estabelecer se houve um desvio do padrão de cuidado aceitável e se esse desvio causou danos ao paciente. As ações legais podem ser movidas para buscar compensação pelos danos, incluindo custos médicos, perda de renda, sofrimento e dor. </h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Compreendemos profundamente a angústia que é enfrentar uma possível negligência médica que afeta diretamente seus direitos de responsabilidade médica. Ao escolher nossa firma, você estará tomando um passo importante em direção à defesa dos seus direitos e à busca por um tratamento médico adequado. Estamos aqui para apoiá-lo e garantir que a responsabilidade médica seja devidamente reconhecida e que você receba a justiça que merece.</h1>
        </div>
        <div className='bg-color-white py-12 px-6 md:px-24'>
            <h1 className='text-color-clicado font-bold text-4xl'>Direito da Propriedade Intelectual</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>É uma área jurídica que abrange a proteção dos direitos relacionados à criatividade e à inovação. Os direitos autorais protegem obras artísticas, literárias e musicais, concedendo aos criadores o controle sobre o uso e a distribuição de suas criações. As marcas registradas protegem os símbolos distintivos de produtos ou serviços, como logotipos e nomes de empresas, evitando que terceiros usem essas marcas de maneira confusa ou enganosa. As patentes, por outro lado, protegem invenções e ideias inovadoras, garantindo aos inventores o direito exclusivo de explorar comercialmente suas criações por um período determinado.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Ele incentiva a inovação ao recompensar os criadores e inventores por seus esforços e investimentos em pesquisa e desenvolvimento. Além disso, o Direito da Propriedade Intelectual desempenha um papel fundamental na proteção da concorrência leal, ao impedir a apropriação indevida de criações alheias.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'> Se você acredita que seus direitos foram violados, nossa equipe está pronta para investigar, reunir evidências sólidas e tomar medidas legais para proteger seus interesses. Estamos comprometidos em defender seus direitos de Propriedade Intelectual com determinação e expertise.</h1>
        </div>
        <div className='bg-color-white py-12 px-6 md:px-24'>
            <h1 className='text-color-clicado font-bold text-4xl'>Direito do contrato</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>O Direito de Contrato é um ramo essencial do Direito Civil que governa as relações contratuais entre as partes envolvidas em acordos legais. Ele estabelece as regras e os princípios que regem a criação, interpretação, execução e rescisão de contratos. Os contratos são acordos voluntários que estipulam os direitos e obrigações das partes envolvidas, e o Direito de Contrato visa garantir que esses acordos sejam justos, executados de forma eficaz e respeitem os interesses de todas as partes.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>É fundamental que os contratos sejam redigidos com clareza e precisão para evitar ambiguidades e litígios futuros. Caso surjam desentendimentos ou violações contratuais, o Direito de Contrato fornece os meios legais para a resolução de disputas, incluindo a busca por indenizações e a execução forçada dos termos acordados. </h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Se você acredita que seus direitos contratuais foram violados ou negligenciados, nossa equipe está pronta para avaliar seu caso, analisar as cláusulas contratuais e buscar soluções legais para proteger seus interesses.</h1>
        </div>
    </div>
  )
}
